body { font: 12px Arial; padding: 0 4%; }

.recharts-cartesian-axis-line,
.recharts-cartesian-axis-tick-line {
  stroke: #fff;
}

.wrapper-box {
  border: 1px solid #ddd;
  border-radius: 3px;
  -webkit-box-shadow: 1px 0px 1px 1px #ddd;
          box-shadow: 1px 0px 1px 1px #ddd
}

.live-graph-box {
  background-color: red;
  color: #fff;
  margin: 10px 1%;
  width: 48%;
  display: inline-block;
}

.changePercentageNum {
  font-weight: 600;
  font-size: 30px;
  margin: 20px 20px 0 20px;
}

.average {
  font-weight: 600;
  font-size: 30px;
  margin: 20px 20px 0 20px;
}

.average > span.big { font-size: 42px; }

select.currency-selector {
  border-radius: 2px;
  border: none;
  border-bottom: 2px solid #fff;
  color: #fff;
  margin: 20px 20px 0 20px;
  padding-bottom: 2px;
  font-size: 24px;
  font-weight: 600;
}

.red { background: #DC143C; }
.darkred { background: darkred; }
.lightgreen { background: green; }
.darkgreen { background: darkgreen; }

.row {
  display: block;
  margin-bottom: 15px;
}

.align-right {
  display: inline-block;
  float: right;
}

.num-tick-box { margin: 0 20px; }
.tick-adjuster { cursor: pointer; }

.tick-number {
  font-size: 24px;
  font-weight: 600;
}



